@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  background-color: #FCFCFC;
  font-family: 'NanumSquare', 'sans-serif';
}

a{
  text-decoration: none;
  color: #000000;
}
.category-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 870px;
    height: 870px;
}

.cateogry-bottom {
    width: 870px;
    height: 670px;
}
.category-title {
    font-size: 30px;
    color: #000000;
    font-weight: bold;
}
.category-list {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 870px;
    height: 40px;
}

.category-items {
    display: flex;
    justify-content: center;
    width: 550px;
}

.category-item {
    text-align: center;
    width: 100px;
    line-height: 40px;
    height: 40px;
}

.category-item.art{
    background-color: #005082;
    color: #ffffff;
}
.category-thumbnail {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 860px;
}

.category-more {
    width: 838px;
    font-size: 14px;
    text-align: right;
    margin-bottom: 20px;
}

.thumbnail-items {
    
}

.thumbnail-item {
    margin-bottom: 40px;
    margin-right: 16px;
    display: inline-block;
    width: 270px;
    height: 150px;
}
.thumbnail-item img{
    display: inline-block;
    width: 270px;
    height: 150px;
}

.thumbnail-item:nth-child(3n) {
    margin-right: 0px;
}


.ranking-container {
    width: 1170px;
    height: 545px;
}

.ranking-title {
    font-size: 30px;
    font-weight: bold;
    color: #ffffff;
    text-align: center;
    margin-bottom: 100px;
}
.ranking-list {
    display: flex;
}

.ranking-item {
    margin-right: 30px;
    width: 270px;
}

.ranking-category {
    text-align: center;
    font-size: 20px;
    color: #ffffff;
    margin-bottom: 25px;
}

.ranking-table {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 270px;
    height: 370px;
    background-color: #ffffff;
}

.ranking-table-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 230px;
    height: 320px;
}

.ranker {
    display: flex;
    align-items: center;
    height: 45px;
    width: 230px;
}

.ranker-profile {
    margin-right: 20px;
}

.ranker-profile img{
    border-radius: 70%;
    width:40px;
    height: 40px;
}

.ranker-info {
    margin-right: 56px;
    height: 40px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.ranker-name {
    font-weight: bold;
}

.ranker-raiting {
    display: flex;
    flex-direction: row;
}

.raiting-score {
    margin-left: 10px;
}

.ranker-tag {
    font-weight: bold;
    font-size: 16px;
}

.ranking-line {
    margin-top: 18px;
    margin-bottom: 18px;
    height: 1px;
    width: 230px;
    background-color: #707070;
}

.lower-ranker {
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    width: 220px;
    height: 20px;
}

.lower-ranker-place {
    font-weight: bold;
    font-size: 16px;
    margin-right: 20px;
}

.lower-ranker-name {
    font-size: 14px;
    margin-right: 75px;
}

.lower-ranker-raiting {
    display: flex;
}
.work-post-container {
    width: 970px;
    height: 1026px;
    margin: 100px auto 0px auto;
}
.work-post-title {
    font-size: 36px;
    text-align: center;
}
.work-post-input-items {
    margin-top: 110px;
}
.work-post-input-item {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
}
.work-post-input-item.top {
    align-items: flex-start;
}
.work-post-input-text {
    display: inline-block;
    width: 90px;
    font-size: 24px;
    margin-right: 50px;
}
.work-post-select {
    padding-left: 20px;
    font-size: 20px;
    width: 150px;
    height: 50px;
}
.work-post-input {
    padding-left: 20px;
    padding-right: 20px;
    font-size: 20px;
    width: 800px;
    height: 50px;
}   
.input-textarea {
    font: 400 13.3333px Arial;
    padding: 20px;
    font-size: 20px;
    width: 800px;
    height: 350px;
}
.work-post-button {
    display: flex;
    justify-content: flex-end;
}
.add-work {
    cursor: pointer;
}
.dalent-header {
    display: flex;
    justify-content: center;
    background-color: #ffffff;
    height:80px;
    box-shadow: 0px 3px 16px #00000029;
}
.header-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1180px;
}
.dalent-logo {
    width: 80px;
    height: 80px;
}
.login-wrapper {
    width: 500px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.login-text {
    color: #303030;
    font-size: 20px;
}
.add-work {
    margin-right: 50px;
    color: #ffffff;
    width:120px;
    text-align: center;
    line-height: 40px;
    height:40px;
    background-color: #005082;
}
.user-info {
    font-size: 14px;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.user-profile {
    border-radius: 70%;
    width: 40px;
    height: 40px;
    margin-left: 10px;
}
.user-name {
    width: 100px;
    font-size: 20px;
}
.add-work.logout {
    background-color: tomato;
}
.add-work.up {
    margin-top: 20px;
    background-color: goldenrod;
}
.add-work.down {
    margin-top: 20px;
    background-color: lightcoral;
}
.add-work.download {
    margin-top: 20px;
    background-color: royalblue;
}
@media screen and (max-width:1700px) {
    .profile-name {
        display: none;
    }
    .profile-intro {
        display: none;
    }
}
.profile {
    top: 160px;
    left: 25%;
    position: absolute;
}
.profile img {
    width: 200px;
    height: 180px;
    border-radius: 70%;
}
.profile-description {
    display: inline-block;
    width:460px;
    height: 100px;
}
.profile-name {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
}
.profile-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    margin-left: 360px;
    margin-right: 30px;
}
.profile-raiting {

}
.profile-raiting-items {
    margin-bottom: 25px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.profile-raiting-items.view {
    margin-top: 20px;
    justify-content: flex-start;
}
.profile-description-top {
    display: flex;
}
.follow-button {
    cursor: pointer;
    margin-left: 50px;
    color: black;
    width:120px;
    text-align: center;
    line-height: 25px;
    height:25px;
    background-color: #fafafa;
    border: 1px solid #dbdbdb;
}
.gallery {
    width: 1170px;
    height: 200px;
}
.profile-items {
    width: 1050px;
    height: 1080px;
}
.profile-item {
    height:190px;
    margin-bottom: 103px;
}
.profile-item-list {
    display: flex;
    margin-top: 20px;
}
.profile-item-img {
    width: 270px;
    height: 150px;
    margin-right: 30px;
}
.profile-item-title {
    align-items: flex-end;
    display: flex;
}
.profile-item-search {
    font-size: 12px;
    margin-left: 10px;
}
.view-details-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 80px);
}
.view-details {
    width: 1180px;
    height: 700px;
}
.view-details-title {
    font-size: 36px;
    font-weight: bold;
}
.view-details-category {
    margin-top: 20px;
    font-size: 24px;
}
.view-details-thumbnail {
    margin-top: 70px;
}
.view-details-thumbnail img {
    width: 540px;
    height: 300px;
}
.view-details-contents {
    line-height: 32px;
    font-size: 20px;
    margin-top: 70px;
}
.login {
    display: flex;
    height: calc(100vh - 80px);
}
.login-left {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50vw;
    height: calc(100vh - 80px);
    background-color: #005082;
}
.login-left-items {
    width:127px;
    height:247px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login-left-items .dalent-logo {
    width: 84px;
    height: 147px;
    margin-bottom: 40px;
}
.login-left-items .dalent-letter {
    width: 127px;
    height: 44px;
}
.login-right {
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login-right-title {
    margin-top: 100px;
    margin-bottom: 200px;
    color: #005082;
    font-size: 40px;
}
.login-right-inputs {
    margin-bottom: 100px;
}
.login-right-input {
    width: 600px;
    margin-bottom: 50px;
}
.login-right-input input {
    
    color: #005082;
    background: none;
    padding: 10px;
    font-size: 24px;
    width: 600px;
    border: none;
}
.login-right-input-line {
    border: 1px solid #005082;
}
.login-right-button input {
    cursor: pointer;
    color: white;
    font-size: 24px;
    width: 600px;
    height: 50px;
    background-color: #005082;
    border: none;
}
.login-right-button-register {
    margin-top: 20px;
}
.login-right-button-register div {
    border: 3px solid #005082;
    color: #005082;
    font-size: 24px;
    text-align: center;
    line-height: 50px;
    width: 600px;
    height: 50px;
    background-color: white;
}
